body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.qanda {
  font-size: .32rem;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  background-color: #f7f7f7; }
  .qanda-header {
    height: 1.5rem;
    background: plum;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 .3rem; }
    .qanda-header-close {
      width: 1rem;
      height: .3rem;
      background: url(data:image/png;base64,) left center/contain no-repeat; }
    .qanda-header-counter {
      border: .01rem solid red; }
    .qanda-header-process {
      border: .01rem solid red;
      width: 1rem;
      display: flex;
      justify-content: flex-end;
      align-items: center; }
  .qanda-wrapper {
    box-sizing: border-box;
    height: calc(100vh - 3.5rem);
    overflow-y: scroll; }
  .qanda-footer {
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center; }
  .qanda-button {
    width: calc(100vw - .6rem);
    height: 1rem;
    border-radius: .5rem;
    background: -webkit-linear-gradient(left, #7E21E6, #4E79F4);
    background: linear-gradient(to right, #7E21E6, #4E79F4);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .28rem;
    color: white;
    font-weight: bold; }

